import React from 'react';
import Helmet from 'react-helmet';
import { injectGlobal } from 'styled-components';

import SEO from '../components/SEO';
import Footer from '../components/Footer';
import favicon from '../../static/logos/favicon.ico';
import config from '../../config/SiteConfig';
import * as palette from '../../config/Style';

/* eslint no-unused-expressions: off */
injectGlobal`
  body {
    background: white;
    color: ${palette.COLOR};
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  
  a {
    color: ${palette.SECONDARY_COLOR}
    text-decoration: none;
    &:hover {
      text-decoration: none;
    }
  }
  
  p a {
    font-weight: 500;
    border-bottom: 1px solid ${palette.LINK_COLOR};
    box-shadow: inset 0 -2px 0px 0px ${palette.LINK_COLOR};
    text-decoration: none;
    
    &:hover {
      text-decoration: none;
      background: ${palette.LINK_HOVER_COLOR};
    }
  }

  .gatsby-resp-image-wrapper {
    margin: 2.75rem 0;
  }
  
  .videoWrapper {
    box-shadow: 0 20px 40px rgba(0,0,0,0.05), 0 15px 12px rgba(0,0,0,0.05);
	position: relative;
	padding-bottom: 56.25%; /* 16:9 */
	padding-top: 25px;
	height: 0;
	iframe {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }
   }
   
  video {
    box-shadow: 0 20px 40px rgba(0,0,0,0.05), 0 15px 12px rgba(0,0,0,0.05);
    max-width: 800px !important;
  }
  .gatsby-image-wrapper {
    box-shadow: 0 20px 40px rgba(0,0,0,0.05), 0 15px 12px rgba(0,0,0,0.05);
    max-width: 800px !important;
  }
  
  @-webkit-keyframes Gradient {
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
  }
  @-moz-keyframes Gradient {
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
  }
  @keyframes Gradient { 
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
  }
  @-ms-keyframes Gradient { 
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
  }
`;

const TemplateWrapper = props => {
  const { children } = props;

  return (
    <div>
      <Helmet
        title={config.siteTitleAlt}
        meta={[
          { name: 'description', content: 'Gatsby Starter Portfolio - Emilia' },
          { name: 'keywords', content: 'gatsby, starter, portfolio, lekoarts' },
        ]}
      >
        <link rel="shortcut icon" href={favicon} />
      </Helmet>
      <SEO />
      {children()}
      <Footer />
    </div>
  );
};

export default TemplateWrapper;



